
@import url("https://fonts.googleapis.com/css?family=Nunito:200,300,400");
@import url("https://cdnjs.cloudflare.com/ajax/libs/normalize/7.0.0/normalize.min.css");
#app {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
}
body {
  perspective: 500;
  -webkit-perspective: 500px;
}
.slide-right-enter {
  min-height: 100%;
}
.slide-right-enter-active, .slide-right-leave-active {
  transition: opacity 1s;
  transform-style: preserve-3d;
}
.slide-right-enter-active {
  animation: slide-right-in 1s ease-in-out;
  animation-delay: 2s;
}
.slide-right-leave-active {
  animation: slide-right-out 1s ease-in-out;
  animation-delay: 2s;
  transform-origin: right;
}
@keyframes slide-right-in {
0% {
    transform: translateX(100%);
    -webkit-transform: translateX(100%);
}
100% {
    transform: translateX(0);
    -webkit-transform: translateX(0);
}
}
@keyframes slide-right-out {
0% {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    z-index: 999999;
    height: 100vh;
    overflow: hidden;
    transform: translateX(0) rotateY(0deg);
    -webkit-transform: translateX(0) rotateY(0deg);
}
100% {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    z-index: 999999;
    height: 100vh;
    overflow: hidden;
    transform: translateX(-100%) rotateY(-27deg);
    -webkit-transform: translateX(-100%) rotateY(-27deg);
}
}
.slide-left-enter {
  min-height: 100%;
}
.slide-left-enter-active {
  animation: slide-left-in 1s ease-in-out;
  animation-delay: 2s;
}
.slide-left-leave-active {
  animation: slide-left-out 1s ease-in-out;
  animation-delay: 2s;
  transform-origin: left;
}
@keyframes slide-left-in {
0% {
    transform: translateX(-100%);
    -webkit-transform: translateX(-100%);
}
100% {
    transform: translateX(0);
    -webkit-transform: translateX(0);
}
}
@keyframes slide-left-out {
0% {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    z-index: 800;
    height: 100vh;
    overflow: hidden;
    transform: translateX(0) rotateY(0deg);
    -webkit-transform: translateX(0) rotateY(0deg);
}
100% {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    z-index: 800;
    height: 100vh;
    overflow: hidden;
    transform: translateX(100%) rotateY(27deg);
    -webkit-transform: translateX(100%) rotateY(27deg);
}
}
* {
  box-sizing: border-box;
}
.cf:before, .cf:after {
  content: '';
  display: table;
}
.cf:after {
  clear: both;
}
.wrapper {
  width: 1500px;
  margin: auto;
}
body {
  position: absolute;
  text-rendering: optimizeLegibility !important;
  margin: 0;
  background: #1c1e27;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;
  font-family: 'Nunito', sans-serif;
  padding: 0;
}
::-webkit-scrollbar {
  width: 5px;
}
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-border-radius: 3px;
}
::-webkit-scrollbar-thumb {
  -webkit-border-radius: 3px;
  border-radius: 3px;
  background: rgba(255, 255, 255, 0.6);
  -webkit-box-shadow: inset 0 0 6px rgba(255, 255, 255, 0.5);
}
::-webkit-scrollbar-thumb:window-inactive {
    background: rgba(255, 255, 255, 0.4);
}
h1 {
  padding: 0;
  margin: 0;
  color: white;
}
h2 {
  margin: 1em;
  color: white;
  text-align: center;
  font-size: 3em;
  font-weight: 300;
}
.content {
  width: 100vw;
}
@media screen and (max-width: 1500px) {
.wrapper {
    width: 1200px;
}
}



