
.preloader[data-v-30ddf6f0] {
  background: white;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  transition: transform 6s ease-in-out;
  transform: scaleX(0.1);
  transform-origin: left;
  height: 3px;
  opacity: 1;
  z-index: 999;
}
.preloader.-hidden[data-v-30ddf6f0] {
    transition: all 6s ease-in-out;
    animation: load-data-v-30ddf6f0 6s forwards;
}
@keyframes load-data-v-30ddf6f0 {
0% {
    transform: scaleX(0.4);
}
100% {
    opacity: 0;
    transform: scaleX(1);
    z-index: -999999;
}
}



